.game {
    box-sizing: border-box;
    width: 320px;
    border: 1px solid white;
    height: 400px;
    color: white;
    padding: 20px;
    display: flex;
    flex-direction: column;
    padding-top: 40px;
}

.text {
    font-size: 20px;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 15px;
}

.text:first-child {
    margin-top: 0px;
}

.input {
    margin: 0 auto;
    width: 200px;
    border: none;
    background-image: none;
    background-color: #1d1c22;
    border: 1px solid #fff;
    padding: 10px;
    box-shadow: none;
    color: white;
    border-radius: 6px
}

.input::placeholder{
    font-size: 12px;
}

.button {
    width: 95%;
    font-size: 20px;
    margin: 0 auto;
    margin-top: 25px;
    border: none;
    background-image: none;
    background-color: #1d1c22;
    padding: 15px 35px;
    box-shadow: none;
    color: white;
    cursor: pointer;
    border-radius: 15px;
    transition: all .4s ease-in-out;
}

.button_inactive {
    width: 95%;
    font-size: 20px;
    margin: 0 auto;
    margin-top: 25px;
    border: none;
    background-image: none;
    background-color: #1d1c22;
    padding: 15px 35px;
    box-shadow: none;
    color: white;
    border-radius: 15px;
    opacity: .3;
}

.button:last-child{
    margin-top: 15px;
    padding: 10px;
}

.button:hover {
    color: black;
    background-color: rgba(150, 150, 150, 0.8);
}